<template>
  <div class="add">
    <header>
      <i class="iconfont icon-fanhui" @click="backNextPage"></i>
      <span>{{ headerName ? "添加地址" : "修改地址" }}</span>
      <i class="iconfont icon-gengduo2"></i>
    </header>
    <section>
      <van-address-edit
        :area-list="areaList"
        :show-delete="!headerName"
        :address-info="addressInfo"
        show-set-default
        show-search-result
        :area-columns-placeholder="['请选择', '请选择', '请选择']"
        @save="onSave"
        @delete="onDelete"
      />
    </section>
    <Tabber class="Tabber"></Tabber>
  </div>
</template>

<script>
import Tabber from "@/components/common/Tabber";
import {
  userAddAddress,
  userChangeList,
  userDeleteAddList,
} from "@/api/useraddress.js";
import Vue from "vue";
import { AddressEdit } from "vant";
import { Toast } from "vant";
Vue.use(AddressEdit);
export default {
  name: "addPathList",
  components: {
    Tabber,
  },
  data() {
    return {
      areaList: {
        province_list: {
          110000: "北京市",
          120000: "天津市",
          130000: "陕西省",
          140000: "甘肃省",
          150000: "河南省",
          160000: "河北省",
          170000: "上海省",
          180000: "四川省",
          190000: "福建省",
        },
        city_list: {
          110100: "北京市",
          120100: "天津市",
          130100: "陕西省",
          140100: "甘肃省",
          150100: "河南省",
          160100: "河北省",
          170100: "上海省",
          180100: "四川省",
          190100: "福建省",
        },
        county_list: {
          110101: "东城区",
          110102: "西城区",
          110105: "朝阳区",
          110106: "丰台区",
          120102: "河东区",
          120103: "河西区",
          120104: "南开区",
          120105: "河北区",
          130101: "西安市",
          130102: "咸阳市",
          130103: "宝鸡市",
          130104: "汉中市",
          130105: "商洛市",
          130106: "榆林市",
          130107: "延安市",
          130108: "安康市",
          130109: "铜川市",
          130110: "安康市",
          140101: "甘肃省",
          150101: "河南省",
          160101: "河北省",
          170101: "上海省",
          180101: "四川省",
          190101: "福建省",
        },
      },
      userAddList: {}, //存储用户新增地址时填入的信息
      headerName: true,
      addressInfo: {}, //vant组件中的初始数据列表
      thisId: "",
    };
  },
  created() {
    this.isUserChangeAdd();
  },
  methods: {
    backNextPage() {
      this.$router.push({
        name: "AddressIndex",
        params: {
          path: this.$route.params?.pathStatus,
        },
      });
    },
    //点击保存按钮触发
    onSave(content) {
      if (this.headerName) {
        this.userAddList["name"] = content.name;
        this.userAddList["tel"] = content.tel;
        this.userAddList["cityCounty"] = content.city + content.county;
        this.userAddList["addressDetail"] = content.addressDetail;
        this.userAddList["areaCode"] = content.areaCode;
        this.userAddList["isDefault"] = content.isDefault == true ? "1" : "0";
        this.sendApiAddress();
      } else {
        this.userAddList["name"] = content.name;
        this.addressInfo["tel"] = content.tel;
        this.addressInfo["cityCounty"] = content.city + content.county;
        this.addressInfo["addressDetail"] = content.addressDetail;
        this.addressInfo["areaCode"] = content.areaCode;
        this.addressInfo["isDefault"] = content.isDefault == true ? "1" : "0";
        this.userchangelist();
      }
    },
    //点击删除按钮时触发
    onDelete() {
      this.userDelAddress();
    },
    async sendApiAddress() {
      let res = await userAddAddress(this.userAddList);
      if (res.data.code == 200) {
        Toast.success("保存成功");
        setTimeout(() => {
          this.$router.back();
        }, 1500);
      } else {
        Toast.fail("保存失败");
      }
    },
    //用户修改地址回调
    async userchangelist() {
      let res = await userChangeList(this.thisId, this.addressInfo);
      if (res.data.code == 200) {
        Toast.success("修改成功");
        setTimeout(() => {
          this.$router.back();
        }, 1000);
      }
    },
    //用户删除地址回调
    async userDelAddress() {
      let res = await userDeleteAddList(this.thisId);
      if (res.data.code == 200) {
        this.$router.back();
      }
    },
    //判断是否是用户地址修改
    isUserChangeAdd() {
      if (this.$route.params?.item) {
        this.headerName = false;
        this.thisId = this.$route.params.item?.id;
        this.addressInfo["name"] = this.$route.params.item?.name;
        this.addressInfo["tel"] = this.$route.params.item?.tel;
        this.addressInfo["addressDetail"] =
          this.$route.params.item?.addressDetail;
        this.addressInfo["areaCode"] = this.$route.params.item?.areaCode;
        this.addressInfo["isDefault"] =
          this.$route.params.item?.isDefault == 1 ? true : false;
      } else {
        return;
      }
    },
  },
};
</script>

<style scoped lang="less">
.add {
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
header {
  box-sizing: border-box;
  width: 100%;
  height: 8%;
  background-color: #b0352f;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 0.5rem;
  i {
    color: #fff;
    font-size: 0.7rem;
    font-weight: 500;
  }
  span {
    text-align: center;
    color: #fff;
    font-size: 0.5rem;
    font-weight: 500;
  }
}
section {
  flex: 1;
  width: 100%;
  overflow: hidden;
}
.Tabber {
  border-top: 1px solid #ccc;
}
</style>
